import React from 'react'

const VideoTitle = ({title, overview}) => {

  
  return (
    <div className='w-full aspect-video pt-[20%] px-24 absolute text-white bg-gradient-to-r from-black'> 
         <h1 className='text-6xl font-bold'>{title}</h1>
         <p className='py-6 text-lg w-1/4 '>{overview}</p>
         <div className='px-6'>
            <button className= ' mx-2 bg-white text-black p-4 px-12 text-xl  rounded-lg hover:bg-opacity-80' >  Play</button>
            <button className=' bg-white text-black p-4 px-9 text-xl bg-opacity-50 rounded-lg'>More Info</button>
         </div>
    </div>
  )
}

export default VideoTitle